.sign {
  margin: 90px auto;
  padding-top: 30px;
  background: #eee;
  height: 500px;
  width: 500px;
  border-radius: 7px;
  box-shadow: 1px 1px 2px #ccc;
  color: rgb(66, 65, 65);
}
.signForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 25px auto;
}

.signForm input,
.signForm button {
  margin-top: 15px;
  font-size: 15px;
  border-radius: 3px;
  width: 50%;
  height: 50%;
  border: 1px solid grey;
}

.sign button {
  margin-top: 40px;
  height: 40px;
  width: 100px;
  background: #fff;
}