.login {
  margin: auto;
  padding-top: 30px;
  background: #eee;
  height: 300px;
  width: 500px;
  border-radius: 7px;
  box-shadow: 1px 1px 2px #ccc;
  color: rgb(66, 65, 65);
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px auto;
}

.login input,
.login button {
  margin-top: 15px;
  font-size: 15px;
  border-radius: 3px;
  width: 50%;
  height: 35px;
  border: 1px solid grey;
}

.signup {
  text-decoration: none;
  color: grey;
  padding-top: 4px;
}

.signup:hover {
  color: black;
}


.login button {
  margin-top: 25px;
  height: 40px;
  width: 100px;
  background: #fff;
}