.App {
  text-align: center;
}

.content {
  height: 96vh;
  background: grey;
  background-size: cover;
  background-position: center;
  color: white;
  justify-content: space-between;
}

.footer {
  background: #282c34;
  color: white;
  height: 4vh;
}

.con1,
.con2 {
  padding-top: 30px;
  text-align: center;
  background: #eee;
  color: gray;
  border-radius: 5px;
}

.con1 {
  margin: 90px 20px 40px 50px;
  flex:1;
  width: 30%;
}

.con2 {
  margin: 90px 40px 40px 30px;
  flex:2;
  width: 70%;
}

@media screen and (max-width: 991px)
{
  .d-flex {flex-direction: column; flex-wrap: wrap; align-content: center;}
  .con1 {flex: 1; width: 70%; height: 50%; margin: 90px 20px 10px 20px;}
  .con2 {flex:2; width: 70%; height: 50%; margin:10px 20px 30px 20px;}}